<template>
  <div id="editPay">
    <common-header title="修改手机"></common-header>
    <div class="input">
      <input type="password" placeholder="新支付密码" v-model="password">
    </div>
    <div class="input">
      <input type="text" placeholder="短信验证码" v-model="code">
      <span class="btn" :class="{'sended': sended}" @click="getSMSCode">{{btnText}}</span>
    </div>
    <div class="info">验证码已发送至手机：{{mobile.substring(0,3) + '****' + mobile.substring(7,11)}}</div>
    <div class="finish__btn" :class="{'active': isFinish}" @click="submit">完成</div>
  </div>
</template>

<script>
  import md5 from 'blueimp-md5';

  export default {
    data() {
      return {
        password: '',
        code: '',
        checkKey: '',
        mobile: '',
        sended: false,        //是否已发送信息
        btnText: '获取验证码'  //按钮上的文字
      }
    },

    computed: {
      isFinish() {
        if(this.password && this.code) {
          return true;
        }else {
          return false;
        }
      }
    },
		created(){
			this.getUser()
		},
    methods: {
    	getUser(){
    		let userInfo = localStorage.getItem('UserDetails')
    		let user = JSON.parse(userInfo)
    		this.mobile = user.mobile
    		console.log(user)
    	},
      //获取验证码
      getSMSCode() {
        if(this.sended) {
          return false;
        }
        
        let count = 10;
        let param = {
          a: 1,
          type: 2,
          mobile: this.mobile,
        }

        this.$Api.SMSCode(param).then((res) => {
          console.log(res);
          if(res.q.s == 0) {
            this.sended = true;
            this.$toast('发送成功!');
            this.checkKey = res.q.checkKey;

            for(let i = count; i >= 0; i--) {
              setTimeout(() => {
                this.btnText = i + 's';
                if(i == 0) {
                  this.btnText = '获取验证码'
                  this.sended = false;
                }
              }, (count - i - 1) * 1000)
            }
          }else {
            this.$toast(res.q.d);
          }
        })
      },

      //提交验证码
      submit() {
        if(!this.isFinish) {
          return false;
        }

        // if(!this.checkKey) {
        //   this.$toast('出错了,请重新获取验证码');
        //   return false;
        // }

        let codeParam = {
          a: 2,
          type: 2,
          mobile: this.mobile,
          code: this.code
        }

        this.$Api.SMSCode(codeParam).then((res) => {
          console.log(res)
          if(res.q.s == 0) {
            let param = {
              a: 2,
              // checkKey: this.checkKey,
              checkKey: '',
              newPassword: md5(this.password)
            }

            this.$Api.UserUpdatePassword(param).then((res) => {
              console.log(res)
              if(res.q.s == 0) {
                this.$toast('修改成功')
              }else {
                this.$toast(res.q.d)
              }
            })
          }else {
            this.$toast('手机验证码出错')
          }
        })
        
      }
    }
  }
</script>


<style lang="scss">
  #editPay {
    .input {
      position: relative;
      margin: 0 0.4rem;
      line-height: 1.2rem;
      height: 1.2rem;
      border-bottom: 1px solid #eee;

      input {
        width: 100%;
      }

      .btn {
        position: absolute;
        top: 0.2rem;
        right: 0;
        width: 2.53rem;
        height: 0.8rem;
        line-height: 0.8rem;
        text-align: center;
        color: #fff;
        background: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
        border-radius: 0.8rem;

        &.sended {
          background: #ccc;
        }
      }
    }

    .info {
      padding: 0 0.4rem;
      padding-top: 0.3rem;
      color: #333;
    }
  }
</style>



